@font-face {
    font-family: 'GothamLight';
    src: url('GothamLight.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'GothamMedium';
    src: url('GothamMedium.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'GothamBook';
    src: url('GothamBook.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'GothamBold';
    src: url('Gotham-Bold.otf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

body {
  font-family: 'GothamMedium' !important;
  font-size: 14px;
  padding: 0; margin: 0;
  overflow-x:hidden;
  background-color: var(--purple) !important;
}
a {text-decoration: none !important;}
a:hover {color: #FFF !important;}

:root {
  --brown:  #54332E;
  --light-brown: #685454;
  --black: #0D080A;
  --dark-green: #2C4449;
  --grey: #9F9E9D;
  --white: #D8D6D4;
  --orange-brown: #9F5B3C;
  --greenish: #968C63;
  --purple: #190c1a;
}

.main-container {
  height:100%;
  width: 90%;
  margin:auto;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.main-header {
  
  background: url("https://www.transparenttextures.com/patterns/60-lines.png"), linear-gradient(-45deg, var(--light-brown), var(--brown), var(--dark-green), var(--greenish));
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
  height: 700px; 
  display: flex;
  flex-direction: column;
  padding:2rem;
}
.main-header-topnav {
  display: flex;
  flex-direction: row;
  padding: 2rem 0;
  color:var(--grey);
}
.main-header-logo {
  font-size: 24px;
  font-family: 'GothamBold';
}
.main-header-navigation {
  margin-left: auto;
}
.main-header-nav {

}
.main-header-navigation a{
  text-decoration: none;
  padding-left: 1.75rem; padding-right: 1.75rem;
  color: var(--grey);
  font-family: 'GothamLight';
  letter-spacing: 2px;
  font-weight:600;
  font-size: 16px;
  transition: .3s;
}
.main-header-navigation-active {
  color:#FFF;
}

.main-header-navigation a:hover{
  color: #FFF;
}

.main-header-headercontent {
  display:flex;
  flex-direction: row;
  height:100%;
  margin-top:20px
}
.main-header-headercontentleft {
 width:50%;
}
.main-header-headercontenth1 {
  font-family: 'GothamBold';
  color:#e9ad68;
  font-size:72px;
  width:100%;
}
.main-header-headercontentleftcontent {
  color:var(--grey);
  font-size:18px;
  width:85%;
}

.main-header-headercontentleftbottom {
  display:flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-top:50px;
  background: rgba(255,255,255,0.07);
  height:60px;
  font-size:15px;
  border: 3px solid rgba(255,255,255,0.1);
  border-radius: 34px 6px;
  font-family:'GothamBold';
  width:80%;
  transition:.3s;
}
.main-header-headercontentleftbottom-div {
  color:var(--grey);
  width:100%;
  height:30%;
  display:flex;
  align-items:center;
  justify-content: center;
  border-right: 2px solid var(--grey);
  transition:.3s;
  cursor:pointer;
}
.bottom-div-noborder {border:none;}
.bottom-div-last {
  border:none;
  background:#e9ad68;
  height:100%;
  color:var(--brown);
  border-radius: 34px 6px;
}
.main-header-headercontentleftbottom-div:hover {
  color:#FFF;
}

.main-header-headercontentright {
  margin-left:auto;
  
 }
 .main-header-headercontentrightcontent {
   background: url('https://media.discordapp.net/attachments/673755194026360833/932766238122319943/565l8u3clbc81.png?width=249&height=443') no-repeat center;
   background-size:cover;
   height:750px;width:500px;
   border-radius: 34px 6px;
   display:flex;
   justify-content: bottom;
 }
 .main-header-headercontentleftbottom:hover {
  transform:scale(1.01);
 }

 .main-header-headercontentrightcontent-credits {
   color:#FFF;
   margin-top: auto;
   padding:6px;
   margin-bottom:6px;
   margin-left:6px;
   border-radius: 14px;
   background:rgba(255,255,255,0.06);
   text-decoration:none;
 }

 .content-art {
   display:flex;
   flex-direction:column;
   margin-top:200px;
   color:#FFF;
 }
 .art-h1 {
   font-family: 'GothamBold';
   font-size:72px;
   color:#e9ad68;
   padding-bottom:50px;
 }

.gallery{
	display: flex;
}

.column{
  display: inline-grid;
  grid-template-columns: 30% 30% 40%;
  gap:5px;
	margin-right: .3vw;
	width: 100%;
}

.item{
	overflow: hidden;
	position: relative;
	margin-bottom: .3vw;
	box-shadow: 0.3rem 0.4rem 0.4rem rgba(0, 0, 0, 0.4);
	border-radius: 14px;
}
.img{
	display: block;
	position: relative;
	transition: .3s;
	width: 100%;
	height: 100%;
}
.imginfo{
  display:flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width:100%;
  height:100%;
	opacity: 0;
	bottom: -.78vw;
  left: 0;
  padding: 6px;
	color: #D8D6D4;
  position: absolute;
  font-size: .65vw;
  border-radius:14px;
  transition: .3s;
  background-color: rgba(0,0,0,0.3);
}

.imglink:hover + .imgtitle + .imgdesc, .imginfo:hover{
	opacity: 1;
  bottom:0;
  backdrop-filter: blur(3px);
}
.imgtitle {
  font-size:21px;
}
.imgdesc{
  font-size: 14px;
  color: #e9ad68;
  display: block;
}
.imginfo-cont {
  border: 3px solid rgba(255,255,255,0.07);
  border-radius:34px 6px;
  padding:15px;
}


.dashboard-card {
  background:transparent;
  color:#FFF;
  margin:auto;
  width:50%;
  border:0;
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 0;
  word-wrap: break-word;

  
}
.dashboard-card-body {
  flex: 1 1 auto;
  padding: 1rem 1rem;
}
.card-forgotetc a {text-decoration: none; color: var(--grey); transition:.3s;}
.card-forgotetc {
  color:#FFF;
  margin-top:1rem;
}
.dashboard-error {
  /* background:#eb3b5a; */
  color: #842029;
  background-color: #f8d7da;
  border-color: #f5c2c7;
  position: relative;
  padding: 1rem 1rem;
  margin-bottom: 1rem;
  border: 1px solid transparent;
  border-radius: 0.25rem;
}
.green {
  background:transparent;
  border: 1px solid#e9ad68;
  color: #FFF;
  font-size: 24px;
  text-align: center;
  margin: auto;
  width: max-content;
  padding: 16px;
  border-radius: 14px;}
.dashboard-card-body-h2{
  margin-bottom:1.5rem;
  text-align:center;
  font-size:72px;
  color:#e9ad68;
}
input[type="email"], input[type="password"] {
  width:100%;
  padding: 20px;
  border-radius: 6px;
  margin-bottom: 20px;
  border: none;
  background: #fff;
  font-size: 16px;
  color: rgb(0, 0, 32);
  outline: none;
}
.input-label {
  display:block;
  padding-bottom: 15px;
  font-size:1rem;

  color: var(--grey);
  font-weight: 300;

}

.dashboard-button {
  display: flex;
  width:max-content;
  justify-content: center;
  margin:auto;
  margin-top:0.5rem;
  color:var(--brown);
  font-weight:bold;
  cursor: pointer;
  background-color: #e9ad68;
  border: 1px solid transparent;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  border-radius: 24px;
  transition:.3s;
    
}
.dashboard-button:hover {
  color:#FFF;
  transform:scale(0.97);
}
.form-group {
  padding:3px;
}

.dashboard-footernav {
  width:100%;
  background: linear-gradient(-45deg, var(--light-brown), var(--brown), var(--dark-green), var(--greenish));
  font-family:'GothamBold';
  background-size:400% 400%;
  animation: gradient 60s ease infinite;
  font-size:15px;
  padding:20px;
  border-top: 3px solid rgba(255,255,255,0.1);
  color:var(--grey);
  overflow:hidden;

}
.dashboard-footernav-nav {
  display:flex;
}
.dashboard-footernav-element {
  color:var(--grey);
  padding-right:1.75rem;
  letter-spacing: 2px;
  font-family:'GothamLight';
  font-weight:600;
  transition:.3s;
  width:max-content;
}

.dashboard-footer-nav-elements {
  display:flex;
  flex-flow:row;
  width:max-content;
  
}
.dashboard-footernav-element:hover {
  color:#FFF;
}
.dashboard-footernav-rightele{
  margin-left:auto; margin-top:auto; margin-bottom: auto;
  display:flex;
  flex-flow:row;
}

.dashboard-card-apply {
  border:1px solid#e9ad68;
  color:#FFF;
  font-size:24px;
  text-align:center;
  margin:auto;
  width:max-content;
  padding:16px;
  border-radius:14px;
}
.dashboard-card-apply-p {
  text-align:center;
  color:var(--grey);
  margin-top:1rem;
  font-size:13px;
}

.form {
  margin-top:15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.form > h1 {
  margin-bottom: 30px;
}

.form > input,
textarea {
  padding: 20px;
  border-radius: 6px;
  /* box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.137); */
  margin-bottom: 20px;
  border: 1px solid lightgray;
  /* border: none; */
  background: #fff;
  font-size: 16px;
  color: rgb(0, 0, 32);
  outline: none;
}

.form > input:focus,
textarea:focus {
  border: #e9ad68;
}

.form > textarea {
  height: 150px;
  
  min-height: 100px;
}

.form > label {
  padding-bottom: 10px;
  color: var(--grey);
  font-weight: bold;
}

.form > button {
  padding: 20px;
  border: none;
  background-color: #e9ad68;
  font-weight: 600;
  font-size: 20px;
  border-radius: 14px;
  color: var(--brown);
  cursor: pointer;
  transition: .3s;
  margin-top: 10px;
}

.form > button:hover {
  color: #FFF;
  transform:scale(0.99)

}
.admin-panel {
  color:#FFF;
  font-size:21px;
  display:flex;
  flex-flow:row;
  width: 100%;
  margin-top:25px;
}
.admin-panel-left {padding:5px;width:100%; border-right:3px solid rgba(255,255,255,0.03)}
.admin-panel-left h1 {
  margin-top:0;
}
.admin-panel-right {margin-left:15px;width:100%}
.admin-panel-right h1 {
  margin:0;
}
.admin-dashboard {
  display: inline-grid;
    gap: 5px;
    grid-template-columns: 30% 30% 30%;
    margin-right: 0.3vw;
    width: 100%;
}

.adminimg {
  max-width:250px;
  width:100%;
  max-height:200px;
  height:100%;
  position: relative;
  transition: .3s;
  margin:3px;
  
}
.admin-item {
  overflow: hidden;
  position: relative;
  margin-bottom: 0.3vw;
  border-radius: 14px;
  
}
.admin-info {
  display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100%;
    opacity: 0;
    bottom: -0.78vw;
    left: 0;
    padding: 6px;
    color: #D8D6D4;
    position: absolute;
    font-size: .65vw;
    border-radius: 14px;
    transition: .3s;
    background-color: rgba(0,0,0,0.3);
}

.admin-imglink:hover + .admin-imgtitle + .admin-imgdesc, .admin-info:hover{
	opacity: 1;
  bottom:0;
  backdrop-filter: blur(3px);
}
.admin-imgtitle {
  font-size:21px;
}
.admin-imgdesc{
  font-size: 14px;
  color: #e9ad68;
  padding:5px;
  display: block;
  cursor:pointer;
}
.admin-imgdesc:hover {color:#FFF;}
.admin-imginfo-cont {
  display:flex;
  flex-direction: column;
  border: 3px solid rgba(255,255,255,0.07);
  border-radius:34px 6px;
  padding:15px;
}